
.content[data-v-5af49713] {
  --color-yes: #01afef;
  --color-mod: #18a246;
  --color-no: #ee0181;
  padding-top:0.2em;
  /* https://bennettfeely.com/gradients/ */
  background: var(--color1);
  background: -moz-linear-gradient(top, var(--color1) 0%, var(--color5) 100%);
  background: -webkit-gradient(
    left top,
    left bottom,
    color-stop(0%, var(--color5)),
    color-stop(100%, var(--color5))
  );
  background: -webkit-linear-gradient(
    top,
    var(--color1) 0%,
    var(--color5) 100%
  );
  background: -o-linear-gradient(top, var(--color1) 0%, var(--color5) 100%);
  background: -ms-linear-gradient(top, var(--color1) 0%, var(--color5) 100%);
  background: linear-gradient(to bottom, var(--color1) 0%, var(--color5) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f85032', endColorstr='#273de6', GradientType=0 );

  /* Set rules to fill background */
  min-height: 100vh;
  min-width: 100vw;

  /* Set up proportionate scaling */
  width: 100%;
  height: auto;
}
.content-moderator[data-v-5af49713] {
  background: var(--color-mod);
  color: white;
}
.content-debate-yes[data-v-5af49713] {
  background: var(--color-yes);
  color: white;
}
.content-debate-no[data-v-5af49713] {
  background: var(--color-no);
  color: white;
}
.speech-bubble[data-v-5af49713] {
  position: relative;
  background: var(--color4);
  color: white;
  padding: 0.75em;
  text-transform: uppercase;
  margin: 0.5em;
  border-radius: 0.2em;
}
.speech-bubble[data-v-5af49713]:after {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  width: 0;
  height: 0;
  border: 20px solid transparent;
  border-right-color: #000000;
  border-right-color: var(--color4);
  border-left: 0;
  border-bottom: 0;
  margin-top: -10px;
  margin-left: -20px;
}
.speech-bubble-no[data-v-5af49713] {
  color: var(--color-no);
  background: white;
}
.speech-bubble-no[data-v-5af49713]:after,.speech-bubble-yes[data-v-5af49713]:after{
  border-right-color: white
}
.speech-bubble-yes[data-v-5af49713] {
  color: var(--color-yes);
  background: white;
}



